button{
  cursor: pointer;
  
}
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}

.chatPage{display:flex;flex-direction:column;align-items:center;background-color:#88FFF7}.chatPage .toggleIcon{font-size:45px;color:#000;cursor:pointer;box-shadow:0px 0px 4px #000001}.chatPage .chat__extDiv{display:flex;align-items:center;justify-content:center;width:100vw;height:100vh}.chatPage .chat__extDiv .chat__sideBar{flex:0.3 1;display:flex;flex-direction:column;font-family:'Times New Roman', Times, serif;color:white;height:90%;background-color:#0F044C;overflow-y:scroll;min-width:271px}.chatPage .chat__extDiv .chat__sideBar .chat__sideBar__heading{display:flex;height:60px;justify-content:center;align-items:center;background-color:#15085a;font-size:x-large}.chatPage .chat__extDiv .chat__sideBar .chat__sideBar__users{display:flex;flex-direction:column;flex:1 1;overflow-y:scroll;font-size:large}.chatPage .chat__extDiv .chat__sideBar .chat__sideBar__users::-webkit-scrollbar{display:none}.chatPage .chat__extDiv .chat__sideBar .chat__sideBar__users .chat__sideBar__user{display:flex;flex-direction:row;padding-left:20px;align-items:center;cursor:pointer;flex:1 1;max-height:60px;transition:1s all ease-in-out;justify-content:flex-start}.chatPage .chat__extDiv .chat__sideBar .chat__sideBar__users .chat__sideBar__user:hover{background-color:#15085a}.chatPage .chat__extDiv .chat__sideBar::-webkit-scrollbar{display:none}.chatPage .chat__extDiv .chat__msgPart{flex:0.7 1;height:90%;padding:10px;background-color:#96BAFF;display:flex;flex-direction:column;justify-content:space-between}.chatPage .chat__extDiv .chat__msgPart .chat__messages{display:flex;flex-direction:column;padding-bottom:10px;overflow-y:scroll}.chatPage .chat__extDiv .chat__msgPart .chat__messages .message__box{display:flex;flex-direction:column;border:1px solid black;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:3px;padding:10px;margin-bottom:10px;background-color:#90ace3ab;min-width:106px;max-width:50%}.chatPage .chat__extDiv .chat__msgPart .chat__messages .message__box__right{display:flex;flex-direction:column;align-self:end}.chatPage .chat__extDiv .chat__msgPart .chat__messages::-webkit-scrollbar{display:none}.chatPage .chat__extDiv .chat__msgPart .chat__messages p{padding:10px}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom{display:flex}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom .chat__inputForm{display:flex;flex:1 1}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom .chat__inputForm form{display:flex;flex:1 1;justify-content:space-around;padding-right:3px}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom .chat__inputForm form input{width:100%;height:40px;border:none;outline:none;padding-left:10px;font-family:"Fira Sans",sans-serif,"Nunito Sans",sans-serif;color:#000;font-weight:900}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom .chat__inputForm form button{min-width:80px;border:none;outline:none;padding-left:3px}.chatPage .chat__extDiv .chat__msgPart .chat__msg__bottom .chat__locationButton button{width:85px;height:100%;border:none;outline:none}@media (max-width: 670px){.chatPage .chat__extDiv .chat__msgPart{margin-right:0px}}

.form__extDiv{width:100%;height:100vh;display:flex;align-items:center;justify-content:center}.form__extDiv .form__container{display:flex;align-items:center;justify-content:center;flex-direction:column;min-width:350px;padding:20px;min-height:400px;background-color:whitesmoke;border-radius:5px;box-shadow:0px 0px 3px #000001}.form__extDiv .form__container form{display:flex;flex-direction:column;justify-content:space-around;width:100%}.form__extDiv .form__container form fieldset{min-height:47px;font-size:35px;margin:10px 0px}.form__extDiv .form__container form fieldset input{border:none;font-size:30px;border-radius:3px;background-color:#f5f5f5;height:40px;padding:10px;width:100%;outline:none}.form__extDiv .form__container form button{height:40px;border-radius:3px;border:none;background-color:#4040f3;cursor:pointer;transition:1s all ease-in;color:white;font-weight:600;font-size:18px;letter-spacing:1px}.form__extDiv .form__container form button:hover{background-color:#04048c;box-shadow:1px 0px 5px #000001;padding-bottom:3px}@media (max-width: 447px){.form__extDiv .form__container{background-color:transparent;box-shadow:none}.form__extDiv .form__container form fieldset input{background-color:transparent}}

